.container {
  min-width: 550px;
  min-height: 450px;
  max-width: 750px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  padding-top: 5px;
  height: fit-content;
  margin: 0 auto;
}

.header {
  display: flex;
  gap: 10px;
  overflow-x: auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.header::-webkit-scrollbar {
  height: 7px;
}

.header::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #c7c7c7;
}

.header .section {
  padding: 10px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
  cursor: pointer;
}

.header .active {
  border-bottom: 2px solid #6c63ff;
  color: #6c63ff;
}

.body {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-top: 0;
}

.detail {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.detail .row {
  display: flex;
  gap: 20px;
}
.detail .column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.detail .row > div {
  flex: 1;
}

.chips {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}

.chips .new {
  color: #6c63ff;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: pointer;
}

.chip {
  display: flex;
  gap: 5px;
  align-items: center;
  padding: 4px 8px;
  background-color: #808080;
  border-radius: 20px;
  cursor: default;
}

.chip p {
  font-weight: 500;
  line-height: 1.25rem;
  color: #fff;
}
.chip svg {
  color: #fff;
  height: 18px;
  width: 18px;
  cursor: pointer;
}

.chips .active {
  background-color: #6c63ff;
}

.body button {
  width: fit-content;
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #6c63ff;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
  transition: 200ms;
}

.body button:active {
  transform: translateY(2px);
}

@media only screen and (min-width: 280px) and (max-width: 480px) {
  .container {
    min-width: 50vw;
    min-height: 50vh;
    padding: 20px;
    align-items: center;
    justify-content: center;
  }
  .header {
    overflow-x: scroll;
    flex-direction: row;
    flex-wrap: wrap; /* Add horizontal scrolling */
  }
  .detail .row {
    flex-direction: column;
    gap: 20px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 768px) {
  .container {
    min-width: auto;
    padding: 25px;
  }
  .header {
    overflow-x: scroll;
    overflow-y: hidden; /* Prevent vertical scrolling */
    white-space: nowrap;
    display: flex; /* Ensure proper layout */
  }
  .header::-webkit-scrollbar {
    height: 7px;
  }
  .header::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: #c7c7c7;
  }
}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .container {
    min-width: auto;
    padding: 30px;
  }
  .header {
    overflow-x: scroll;
    overflow-y: hidden; /* Prevent vertical scrolling */
    white-space: nowrap;
    display: flex; /* Ensure proper layout */
  }
  .header::-webkit-scrollbar {
    height: 7px;
  }
  .header::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: #c7c7c7;
  }
}
